.dashboard{
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.dashboard h2{
    font-size: 2.2em;
    margin-bottom: .5em;
}

.dashboard p{
    color: #AAA;
    margin-bottom: 1em;
}

.noposts {
    text-align: center;
}

.noposts p{
    margin-bottom: 1.5em;
}

.noposts a{
    padding: 10px 25px;
}

.post_header, .post_row{
    display: flex;
    justify-content: space-between;
    width: 80%;
    padding: 10px;
}

.post_header{
    font-weight: bold;
    border-bottom: 2px solid #CCC;
}

.post_row{
    align-items: center;
    border-bottom: 1px solid #eee;
}

.post_row p{
    color: #000;
}

.post_row button,
.post_row a{
    margin: 0 5px;
    height: 30px;
    width: 100px;
    font-size: .7em;
}

@media screen and (max-width: 700px){
    .edictions{
        display: flex;
        flex-direction: column;
    }

    .post_header span{
        margin: 0px 20px;
    }

    .post_row a{
        padding: 0 !important;
        line-height: 30px;
        margin: 0 3px !important;
        box-sizing: border-box;
    }

    .post_row button{
        margin: 5px 0;
        width: 100%;
        border-radius: 9px !important;
        color: red !important;
    }

    .post_row button:hover{
        color: rgb(255, 255, 255) !important;
    }
}