.home{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.search_form{
    max-width: 100%;
    width: 60%;
    display: flex;
    justify-content: center;
    margin-bottom: 2em;
}

.search_form input{
    margin-right: 10px;
    width: 50%;
}

.noposts {
    text-align: center;
}

.noposts p{
    margin-bottom: 1.5em;
}

.noposts a{
    padding: 10px 25px;
}

@media screen and (max-width: 700px){
    .home h1{
        font-size: 1.5em;
        width: 90%;
        margin: 2em 0;
    }
}